.calendar {
  border: 1.5px solid #ccc;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  margin: 20px auto;
  padding: 10px;
  font-family: sans-serif;
  overflow: hidden;
  box-shadow: 0 4px 12px #0000001a;
}

.header {
  background-color: var(--font-color-2);
  color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
  display: flex;
}

.header button {
  color: #fff;
  opacity: .7;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 15px;
}

.day-names, .days {
  flex-wrap: wrap;
  display: flex;
}

.day-name, .day, .empty-day {
  box-sizing: border-box;
  text-align: center;
  opacity: .7;
  border: 1px solid #0bd3f2;
  border-radius: 2.5px;
  width: 14.28%;
  padding: 10px;
  font-size: 12px;
}

.day-name {
  opacity: .8;
  background-color: #6cc6f3;
  margin-bottom: 8px;
  font-weight: bold;
}

.day {
  color: #fff;
  cursor: pointer;
  border: 2px solid #fff;
  transition: background-color .3s;
}

.day:hover {
  background-color: #ccc;
}

.empty-day {
  background-color: #f9f9f9;
  border: 2px solid #fff;
}

.today, .selected {
  color: #fff;
  text-align: center;
  background-color: #87ceeb;
  border-radius: 5px;
}
